




















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import FormItem from '@/components/Money/FormItem.vue';
import Button from '@/components/Button.vue';

const map: { [key: string]: string } = {
  'tag name duplicated': '标签名重复了'
};

@Component({
  components: {Button, FormItem}
})
export default class createTags extends Vue {
  h = '';

  created() {
    this.$store.commit('fetchTags');
  }

  mounted(){
    this.h = `${document.body.clientHeight}`;
  }

  get tagList() {
    return this.$store.state.tagList;
  }

  get newTagType() {
    return this.$route.query.newTagType;
  }

  // eslint-disable-next-line no-undef
  tag: Tag = {id: '', name: '', type: (this.newTagType as string)};

  createTag() {
    const name = this.tag.name;
    const type = this.tag.type;
    if (!type) { return this.$toast('标签类型没了哦！ 请返回重新点击新建标签按钮！');}
    if (!name) { return this.$toast('标签名不能为空！'); }
    this.$store.commit('createTag', {name, type});
    if (this.$store.state.createTagError) {
      window.alert(map[this.$store.state.createTagError.message] || '未知错误');
    } else {
      this.$toast('标签创建成功')
      this.tag.name = '';
    }
  }

  returnType(type: string){
    this.$router.push({
      path:'/labels',
      query:{returnTagType: type},
    })
  }
}
